import React, { useState } from "react";
import "./QuestionCard.css";

const QuestionCard = ({
  question,
  chapterNumber,
  chapterName,
  sectionIndex,
  sectionTotal,
  onAnswer,
  onBack
}) => {
  const [selected, setSelected] = useState("");

  const { text, type, options, section } = question;

  const likert = [
    "ğŸ˜ Strongly Agree",
    "ğŸ™‚ Agree",
    "ğŸ˜ Neutral",
    "ğŸ˜• Disagree",
    "ğŸ˜¡ Strongly Disagree",
  ];

  const answerOptions = type === "likert" ? likert : options;

  return (
    <div className="question-card fade-in">

      {/* NEW HEADER */}
      <div className="q-header">

        {/* Left side: Chapter Name */}
        <div className="chapter-title">
          <span className="chapter-number">Chapter {chapterNumber}.</span>
          <span className="chapter-name">{chapterName}</span>
        </div>

        {/* Right side: Section Progress */}
        <div className="section-progress">
          {sectionIndex} / {sectionTotal}
        </div>

      </div>

      {/* Question text */}
      <h2 className="q-text">{text}</h2>

      {/* Options */}
      {type !== "text" && (
        <div className="q-options">
          {answerOptions.map((opt, idx) => (
            <button
              key={idx}
              className={`q-option ${selected === opt ? "selected" : ""}`}
              onClick={() => setSelected(opt)}
            >
              {opt}
            </button>
          ))}
        </div>
      )}

      {/* Text Input */}
      {type === "text" && (
        <textarea
          className="q-textarea"
          placeholder="Write your answer..."
          value={selected}
          onChange={(e) => setSelected(e.target.value)}
        />
      )}

      {/* Footer */}
      <div className="q-footer">
        {onBack && (
          <button className="q-btn back-btn" onClick={onBack}>
            â† Back
          </button>
        )}

        <button
          className={`q-btn next-btn ${selected ? "active" : ""}`}
          disabled={!selected}
          onClick={() => {
            if (!selected) return;
            onAnswer(selected);
            setSelected("");
          }}
        >
          Next â†’
        </button>
      </div>
    </div>
  );
};

export default QuestionCard;
